import React,{useState} from 'react'
import './mainpage-hegiht.css'
import Nav from './Nav'
import SearchForm from './SearchForm'
import BgShape from './bgShape'
import InsideNav from './InsideNav'
import Button from '@material-ui/core/Button';
import CardContainer from './CardContainer'
import musagim from './musagim'

const MainPage = () => {
    let [word, setWord] = useState('')

    const changeHandler = (e) => {
        e.preventDefault();
        //  setWord(e.target.value)
         console.log(e.target.value);
    }
    
        if (word.length > 0) {
            musagim = musagim.filter(i => i.title.match(word))
        }
    
    const createCardInfo = cardInfo => {
    //     id: 1,
    //     title: 'אריסטו',
    //     subTitle: '(384–322 לפנה"ס)',
    //     content: 'היה פילוסוף יווני, מבכירי הפילוסופים של העת העתיקה, ומאבות הפילוסופיה המערבית.',
    //     lastUpdate: new Date().toLocaleString(),
    //     favorite: false,
    //     shareLink: `http://aristo.cf/main/`
        return <CardContainer 
            key={cardInfo.id}
            id={cardInfo.id}
            title={cardInfo.title}
            subTitle={cardInfo.subTitle}
            content={cardInfo.content}
            lastUpdate={cardInfo.lastUpdate}
            shareLink={cardInfo.shareLink}
        />
    }
    const latters = ['א','ב','ג','ד','ה','ו','ז','ח','ט','י','כ','ל','מ','נ','ס','ע','פ','צ','ק','ר','ש','ת'];
    return (
        <div>
           <BgShape/>
           <Nav/>
            <div className="container-fluid">
               <div className="row mt-5">
                <div className="col-lg-3 col-sm-2"></div> 
                <div className="col-lg-6 col-sm-8">
                    {/* <SearchForm/> */}
                    <div>
                        <input
                            className="mx-auto col text-center h6 d-flex w-50 border-0 rounded shadow-none bg-dark"
                            type="text"
                            autoFocus
                            placeholder="חיפוש חופשי"
                            onChange={changeHandler}
                            value={word}                        
                        />
                    </div>
                </div> 
                <div className="col-lg-3 col-sm-2"></div>
            </div>
            <p className="p text-center text-secondary m-0">או</p>
            <div className="bg-light text-dark py-0 my-2 w-25 mx-auto rounded">
                <p className="p  text-center text-secondary m-0">חיפוש לפי אות</p>
            </div>
            <div className="container mb-5">
                <div className="row mx-auto">
                    <div className="row mx-auto">
                        {}
                    {latters.map(item => {
                        return <div key={item} className="col p-1 mx-auto justify-content-center text-light text-center">
                            <Button variant="outlined" className="text-light btn btn-lg btn-block ed shadow-none">
                            {item}
                            </Button>
                        </div>
                    })}
                    </div>
                </div>
            </div>
           </div>
           <div className="container-fluid bg-light">
                <div className="row m-1 d-flex">
                    <div className="col col-lg-11 col-sm-12 m-0 p-0 justify-content-center mx-auto">
                        {
                            musagim.map(createCardInfo)
                        }
                    </div>
                </div>
           </div>
           <div className="d-flex justify-content-center fixed-bottom btn-click" >
                <InsideNav />
            </div>
        </div>
    )
}
export default MainPage